body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.welcome-buttons-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 200px;
}

.welcome-button {
  background-color: var(--primary);
  color: var(--on-primary);
  border: none;
  border-radius: 0.5em;
  padding: 1em 2em;
  width: 288px;
  max-width: 100%;
}

.welcome-button:hover {
  cursor: pointer;
}



.edit-timer-list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 43px;
}

.edit-timer-list-item label {
  width: 50%;
}

.workout-units {
  width: 20%;
}

.fa-icon:hover {
  cursor: pointer;
}

.font-awesome-buttons {
  background-color: transparent;
  border: none;
  padding: 0;
}

.font-awesome-buttons:active {
  color: var(--primary);;
}

.font-awesome-buttons:focus {
  background-color: transparent;
}

.form-input {
  margin-bottom: 0 !important;
  text-align: center;
}

.form-input:disabled {
  border-bottom: none !important;
  color: var(--on-background) !important;
}

.continuous-icon-div {
  width: 45%;
}

.button-div {
  display: flex;
  justify-content: space-evenly;
}

.form-button {
  background-color: var(--primary);
  color: var(--on-primary);
  border: none;
  border-radius: 8px;
  padding: 1em 2em;
}

.form-button:focus {
  background-color: var(--primary);
}

.form-button:hover {
  cursor: pointer;
  background-color: var(--primary-dark);
}
.timer-labels-div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title-label {
  font-size: 2em;
}

.reps-sets-labels {
  font-size: 2em;
}

#stage {
  font-size: 3em;
}

#countdown {
  font-size: 6em;
}

.timer-button-div {
  display: flex;
  justify-content: space-evenly;
}

.timer-buttons {
  background-color: var(--primary);
  color: var(--on-primary);
  padding: 0.5em 1em;
  border: none;
  border-radius: 0.5em;
}

.timer-buttons:focus {
  background-color: var(--primary);
  color: var(--on-primary);
}

.timer-buttons:hover {
  cursor: pointer;
}


.collapsible-body-div {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.collapsible-body-div button {
  padding: 0.2em 0.5em;
  background-color: var(--primary);
  color: var(--on-primary);
  border: none;
  border-radius: 0.5em;
}

.collapsible-body-div button:hover {
  cursor: pointer;
}

.google-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: var(--primary);
  color: var(--on-primary);
  border: none;
  padding: 0.5em 1em;
}

.google-button:focus {
  background-color: var(--primary);
  color: var(--on-primary);
}

.google-button:hover {
  cursor: pointer;
}

.sign-in-out {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding-top: 1em;
}

.sign-in-out span {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
footer {
  padding-top: 2em;
  padding-bottom: 2em;
  background-color: var(--primary);
  color: var(--on-primary);
  margin-top: auto;
}

.footer-row {
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: 0;
}

.links-div {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.contact-div {
  display: flex;
  flex-direction: column;
}

.contact-div label {
  color: var(--on-primary);
}

footer .links-div-anchor {
  margin-left: 16px;
  color: var(--on-primary);
}

.social-icon {
  color: var(--on-primary);
}

/* .social-icon:hover {
  color: var(--primary-light);
} */

.font-awesome {
  font-size: 2em;
}
:root {
  --primary: #ee6e73;
  --primary-light: #ff9fa2;
  --primary-dark: #b73d48;
  --on-primary: white;
  --on-primary-light: white;
  --on-primary-dark: white;
  --background: white;
  --on-background: black;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  max-width: 360px !important;
  min-height: 100%;
  flex: 1 1;
  padding-bottom: 2em;
}
